                    font-size: 12px;
                    color: #7d7d7d;
                    padding: 8px 0;
                  }
                }
              }
            }
          }
          /*--------  end --------*/
        }
      }
    }
    /*--------  end --------*/
    /*-------foot start----------*/
    .foot-classify {
      .index {
        background-position: center -250px;
        color: #555;
      }
      .homeCategorySearch {
        background-position: center -50px;
        color: #39ac69;
      }
      .cartList {
        background-position: center -350px;
        color: #555;
      }
      .orderList {
        background-position: center -400px;
        color: #555;
      }
      .mine {
        background-position: center -450px;
        color: #555;
      }
    }
    /*-------foot end----------*/
  }
}
</style>



