<template>
    <div class="a5e">
        <!--  -->
        <div class="a5k">
            <div></div>
        </div>
        <!--  -->                

        <div class="a5r">
            <p class="a5s"></p>
            <ul class="a5t"></ul>
        </div>

        <!--  -->
        <div class="a5n"></div>
        <!--  -->
        

        <!--  -->
        <a href="javascript:void 0;" class="a5f outcartcontent show" :class="{light:cars.length > 0}"
             @click="showlist()" v-if="showList">
            <!--  -->
            <i class="a5g" v-if="cars.length>0">{{numAll}}</i>
            <!--  -->                    
        </a>
        <!--  -->

        <!--  -->
        <div class="a5h" :class="{transX:!showList}">
            <div class="a5i" v-if="cars.length == 0"></div>   
            <div v-if="cars.length != 0">{{priceAll}}</div>
        </div>
        <!--  -->

        <!--  -->
        <a href="javascript:void 0;" class="a5j" :class="{disabled:cars.length == 0}"></a>
        <!--  -->

        <!--  -->
        <div class="minicart-content" :class="{transY:!showList}" v-if="cars.length>0">
            <!-- logo -->
            <a href="javascript:void 0;" class="a5f incartcontent light" :class="{show:!showList}" @click="showlist()">
                <i class="a5g">{{numAll}}</i>
            </a>
            <!-- logo -->
            <!--  -->
            <i class="a5v"></i>
            <!--  -->
            
            <!-- & -->
            <div class="a5w">
                <span class="a72 a73 checked"></span>
                <p class="a75">({{numAll}}{{KG}}g)</p>
                <a href="javascript:void 0;" class="a5x"></a>
            </div>
            <!-- & -->

            <!--  -->
            <div class="a5z">
                <div class="a6a single">
                    <!--  -->
                    <ul class="minicart-goods-list single">
                        <li class="a6c single" v-for="(car,index) in cars" :key="index">
                            <span class="a72 a6d checked"></span>
                            <a href="javascript:void 0;" class="a6j">
                                <table class="a6k">
                                    <tbody>
                                        <tr>
                                            <td style=" width:62px;">
                                                <img :src="'/static/upload/'+car.imgurl" class="a6l">
                                            </td>
                                            <td>
                                                <div class="a6o">{{car.name}}{{car.kg}}g</div>
                                                <div class="a6x"> <span class="a6s" style="background: rgb(95, 188, 101);">{{car.type}}</span>  <div class="a6e">{{car.sprice}} <del class="a6f">{{car.price}}</del></div></div>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                                <!--  -->
                                <a href="javascript:void 0;" class="a6m" @click="subcar(car)"></a>
                                <em style="" class="a6p">{{car.num}}</em>
                                <a href="javascript:void 0;" class="a6n" @click="addcar(car)"></a>
                                <!--  -->                                                                
                            </a>
                        </li>
                    </ul>
                    <!--  -->
                    
                </div>
            </div>
            <!--  -->
            
            <!--  -->
            <div style="height: 49px;"></div>
            <!--  -->
            
            <!--  -->
            <div style="display: none;" class="a76"></div>
            <!--  -->                    

           
            
            </div>
        <!--  -->
         <!--  -->
          <div class="a5o" @click="showlist()" :class="{dis:!showList}"></div>
        <!--  -->
        
    </div>
</template>

<script>
import store from "store/dist/store.everything.min.js";

export default {
  name: "car",
  props: ["shopid"],
  data() {
    return {
      cars: [],
      showList: true
    };
  },
  created() {
    var th = this;
    //add
    this.$root.bus.$on("add", function(value) {
      for (var i = 0; i < th.cars.length; i++) {
        if (value.id == th.cars[i].id) {
          th.cars.splice(i, 1, value); //
          return;
        }
      }
      th.cars.push(value);
    });

    //sub
    this.$root.bus.$on("sub", function(value) {
      for (var i = 0; i < th.cars.length; i++) {
        if (value.id == th.cars[i].id) {
          if (value.num == 0) {
            th.cars.splice(i, 1);
            return;
          }
          th.cars.splice(i, 1, value);
          return;
        }
      }
      th.cars.push(value);
    });

    var scars = store.get("car");
    if (scars[this.shopid]) {
      //
      for (let i = 0; i < scars[this.shopid].length; i++) {
        this.cars.push(scars[this.shopid][i]);
      }
    }
  },
  computed: {
    numAll() {
      var numAll = 0;
      for (var i = 0; i < this.cars.length; i++) {
        numAll += this.cars[i].num;
      }
      return numAll;
    },
    priceAll() {
      var priceAll = 0;
      for (var i = 0; i < this.cars.length; i++) {
        priceAll += this.cars[i].sprice * this.cars[i].num;
      }
      return priceAll;
    },
    KG() {
      var KG = 0;
      for (var i = 0; i < this.cars.length; i++) {
        KG += this.cars[i].kg * this.cars[i].num;
      }
      return KG;
    }
  },
  methods: {
    addcar(car) {
      var Car = store.get("car"); //
      for (let i = 0; i < this.cars.length; i++) {
        if (this.cars[i].id == car.id) {
          //
          if (this.cars[i].num >= car.store) {
            return;
          }
          this.cars[i].num += 1;
          Car[this.shopid][i].num += 1;
          this.cars.splice(i, 1, this.cars[i]); //
          car.num = Car[this.shopid][i].num;

          //plist
          this.$root.bus.$emit("addcar", car);

          store.set("car", Car);
          return;
        }
      }
    },
    subcar(car) {
      var Car = store.get("car"); //
      for (let i = 0; i < this.cars.length; i++) {
        if (this.cars[i].id == car.id) {
          this.cars[i].num -= 1;
          Car[this.shopid][i].num -= 1;
          this.cars.splice(i, 1, this.cars[i]);

          if (this.cars[i].num <= 0) {
            //0cars
            this.cars.splice(i, 1);
            this.showList = !this.showList;
          }
          if (Car[this.shopid][i].num <= 0) {
            Car[this.shopid].splice(i, 1);
          }

          //plist
          this.$root.bus.$emit("subcar", car);

          store.set("car", Car);
          return;
        }
      }
    },
    showlist() {
      if (this.cars.length > 0) {
        this.showList = !this.showList;
      }
    }
  }
};
</script>

<style>

</style>



