<template>
    <ul>
		<li v-for="(plist,index) in plists" :key="index">
			<a class="a2 linksGoods">
				<span class="adc">
                    <img class="pic" :src="'/static/upload/'+plist.imgurl" />
                    <span v-if="plist.rest"><var></var></span>
				</span>
				<dl>
					<dt>{{plist.name}}{{plist.kg}}g</dt>
                    <dd class="adf"><strong class="line_pre">{{plist.sell}}</strong></dd>
                    <dd v-if="plist.type"><span class="a2k" style="background:#5FBC65;">{{plist.type}}</span></dd>
                    <dd class="a3p">
                        <label><em></em>{{plist.sprice}}</label>
                        <strong class="a3q"><em></em>{{plist.price}}</strong>
                    </dd>
                </dl>
            </a>
            <div class="Box">
                <span class="reduce" v-if="nums[plist.id]>0" @click="sub(plist)"></span>
                <label v-if="nums[plist.id]>0">{{nums[plist.id]}}</label>
                <span class="add" @click="add(plist)"></span>
            </div>
        </li>
    </ul>
</template>

<script>
import $ from "jquery/dist/jquery.min.js";
import store from "store/dist/store.everything.min.js";

export default {
  name: "plists",
  props: ["shopid"],
  data() {
    return {
      plists: [],
      nums: {}
    };
  },
  created() {
    var baseUrl = "http://easy-mock.com/mock/59f6fe2dee3c3f29ab2fced0/daojia";
    var plistsUrl = baseUrl + "/plists";
    var th = this;
    $.get(plistsUrl, {}, function(res) {
      for (var i = 0; i < res.plists.length; i++) {
        th.plists.push(res.plists[i]);
      }
    });

    //addcar
    this.$root.bus.$on('addcar',function(value){
      for(var i=0;i<th.plists.length;i++){
        if(value.id == th.plists[i].id){
          th.plists.splice(i,1,value);
          th.nums[th.plists[i].id] = th.plists[i].num;    //nums       
          return;
        }
      }
    });


    //subcar
    this.$root.bus.$on('subcar',function(value){
      for(var i=0;i<th.plists.length;i++){
        if(value.id == th.plists[i].id){
          th.plists.splice(i,1,value);
          th.nums[th.plists[i].id] = th.plists[i].num;  //nums 
          return;
        }
      }
    });
  },
  methods: {
    sub(plist) {
      var car = store.get("car"); //
      for (let i = 0; i < car[this.shopid].length; i++) {
        //
        if (plist.id == car[this.shopid][i].id) {  //
          car[this.shopid][i].num -= 1; //-1
          this.nums[plist.id] -= 1; //nums-1
          plist.num = car[this.shopid][i].num;          
          //0
          if (car[this.shopid][i].num == 0) {
            car[this.shopid].splice(i, 1);
          }
          store.set("car", car); //
          //
          this.$root.bus.$emit("sub", plist);
          return false;
        }
      }
    },
    add(plist) {
      //
      //
      //
      var car = store.get("car");
      //
      if (car[this.shopid]) {
        //
        //
        for (let i = 0; i < car[this.shopid].length; i++) {
          let cur = car[this.shopid][i];
          if (plist.id == cur.id) {
            //
            if (this.nums[plist.id] < plist.store) {
              car[this.shopid][i].num += 1;
              this.$set(this.nums, plist.id, car[this.shopid][i].num);
              //car
              plist.num = car[this.shopid][i].num;
              this.$root.bus.$emit("add", plist);
            }
            //+1
            store.set("car", car);
            return false;
          }
        }
        //
        plist.num = 1;
        car[this.shopid].push(plist);
        store.set("car", car);
      } else {
        //
        //id
        car[this.shopid] = [];
        //,
        plist.num = 1;
        car[this.shopid].push(plist);
        store.set("car", car);
      }
      this.$set(this.nums, plist.id, 1);
      //
      this.$root.bus.$emit("add", plist);
    }
  }
};
</script>

<style>

</style>



